import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE, MainColor } from '@/common/constants';
import { REFRESH_USERINFO } from "@/common/globalMsgKeys";
/**
 * accountLogin 登录界面-用于账户密码登录
 * @description 登录界面-用于账户密码登录
 * @Author: 穆兰
 * @Date: 2022/1/5
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/10
 */
export default {
    data() {
        return {
            username: '',
            password: '',
            agreest: false,
            MainColor
        };
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @event {Function()} 点击同意checkbox
         */
        onCheckBoxClick() {
            this.agreest = !this.agreest;
        },
        /**
         * @event {Function()} 输入密码的input响应事件
         * @param {Object} e 事件
         */
        onPasswordInput(e) {
            this.password = e;
        },
        /**
         * @event {Function()} 输入用户名的input响应事件
         * @param {Object} e 事件
         */
        onUsernameInput(e) {
            this.username = e;
        },
        /**
         * @description 点击用户协议文字
         */
        onUserProtocolClick() {
            uni.navigateTo({
                url: "/pages/login/bugu-use-protocol/bugu-use-protocol"
            });
        },
        /**
         * @description 点击隐私政策文字
         */
        onPrivacyPolicyClick() {
            uni.navigateTo({
                url: "/pages/login/bugu-privacy-policy/bugu-privacy-policy"
            });
        },
        /**
         * @description 点击登录按钮
         */
        onLoginButtonClick() {
            if (this.username && this.password) {
                if (this.agreest) {
                    this.tryToLogin(this.username, this.password);
                }
                else {
                    uni.showToast({
                        title: "请先同意用户协议和隐私政策",
                        icon: 'none'
                    });
                }
            }
            else {
                uni.showToast({
                    title: "请先输入用户名或密码",
                    icon: 'none'
                });
            }
        },
        /**
         * @description 通过用户名和密码登录
         * @param username 用户名
         * @param password 密码
         * @returns {null}
         */
        async tryToLogin(username, password) {
            uni.showLoading({
                title: '正在登陆'
            });
            try {
                let res = await request({
                    data: {
                        method: 'POST',
                        group: 'user',
                        action: 'login/byPassword',
                        data: {
                            username: username,
                            password: password
                        },
                        header: {
                            'content-type': 'application/x-www-form-urlencoded' // 默认值
                        },
                    }
                });
                uni.hideLoading();
                if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                    console.log("登录成功");
                    await uni.setStorage({
                        key: 'token',
                        data: res.data.data.token
                    });
                    await uni.setStorage({
                        key: 'userInfo',
                        data: res.data.data.userInfo
                    });
                    console.log(res.data.data);
                    uni.showToast({
                        title: '登录成功',
                        icon: 'success'
                    });
                    uni.$emit(REFRESH_USERINFO, { needRefresh: true });
                    uni.navigateBack({
                        delta: 2
                    });
                }
            }
            catch {
                uni.showToast({
                    title: '登录失败，请稍后再试',
                    icon: 'none'
                });
            }
        }
    },
};
